@media (min-width: 992px) {
  .header__menu {
    justify-content: flex-end;
    align-items: center;
    gap: 100px;
    display: flex;
  }

  .header__menu:before {
    display: none;
  }

  .menu__wrapper {
    justify-content: space-between;
    display: flex;
  }

  .nav__navigation {
    gap: 40px;
    display: flex;
  }
}

.nav__btn {
  display: none;
}

.no-scroll {
  overflow: hidden;
}

.header__menu {
  padding-left: 15px;
  padding-right: 15px;
}

.nav__link {
  color: #263238;
  font-size: 22px;
  font-weight: 500;
}

.header__menu:before {
  content: "";
  height: 2px;
  width: 95%;
  background-color: #8f95a5;
  position: absolute;
  top: 90px;
  left: 50%;
  transform: translateX(-50%);
}

@media (max-width: 993px) {
  .btn-menu {
    width: 30px;
    height: 30px;
    display: block;
    position: relative;
  }

  .nav__navigation {
    flex-direction: column;
    gap: 20px;
    display: flex;
  }

  .btn-menu__icon, .btn-menu__icon:before, .btn-menu__icon:after {
    content: "";
    width: 100%;
    height: 2px;
    background-color: #000;
    transition: top .1s ease-in;
    position: absolute;
    left: 0;
  }

  .btn-menu__icon {
    top: calc(50% - 1px);
  }

  .btn-menu__icon:after {
    width: 60%;
    top: 8px;
  }

  .btn-menu__icon:before {
    top: -8px;
  }

  .menu-open .btn-menu__icon:before, .menu-open .btn-menu__icon:after {
    width: 100%;
    top: 0;
  }

  .menu-open .btn-menu__icon {
    transform: rotate(-45deg);
  }

  .menu-open .btn-menu__icon:after {
    transform: rotate(90deg);
  }

  .header__menu {
    width: 100%;
    height: 100%;
    background-color: #fff;
    flex-direction: column;
    align-items: flex-start;
    row-gap: 70px;
    padding-top: 130px;
    transition: transform .3s ease-in;
    display: flex;
    position: fixed;
    top: 0;
    left: 0;
    transform: translateX(-100%);
  }

  .menu-open .header__menu {
    transform: translateX(0);
  }
}

@media (min-width: 575px) {
  .header__menu {
    row-gap: 80px;
    padding-left: 35px;
    padding-right: 35px;
  }

  .header__menu:before {
    width: 91%;
  }

  .nav__link {
    font-size: 24px;
  }
}

@media (min-width: 767px) {
  .header__menu {
    width: 50%;
