    padding-left: 40px;
    padding-right: 40px;
  }

  .header__menu:before {
    width: 85%;
  }

  .menu-open .header__menu:after {
    content: "";
    width: 100%;
    z-index: -999;
    background-color: rgba(82, 101, 110, .584);
    position: absolute;
    top: 0;
    bottom: 0;
