.services{
    // margin: 0 auto;
    // padding: 0 35px;
    &__container{
        display: flex;
        flex-direction: column;
        align-items: center;
        // padding: 0 45px;
        gap: 81px;
    }
    &__title{
        font-weight: 600;
        font-size: 24px;
        // margin-bottom: ;
    }
    &__cases{
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 65px;
    }
}
.case{
    display: flex;
    flex-direction: column;
    align-items: center;
    // padding: 0 20px;
    // gap: 25px;
    // max-width: 280px;
    &__title{
        font-weight: 600;
        font-size: 18px;
        color: $colorTextAccent;
        text-align: center; 
        margin-bottom: 17px;
    }
    &__text{
        text-align: center; 
        color: $colorTextSecondary;
        margin-bottom: 33px;
    }
    &__fare{
        font-size: 18px;
        color: $colorTextAccent;
        padding: 5px 15px;
        background-color: $colorFillPrimary;
        border-radius: 5px;
        display: flex;
        gap: 12px;
        // align-items: center;
        line-height: 1.5;
        &::after{
            content:  'per month';
            display: flex;
            // display: block;
            align-items: center;
            font-size: 14px;
            color: $colorTextFourth;
        }
    }
    &__photo{
        margin-bottom: 28px;
    }
}
@media (min-width: 575px){
    .services{
        margin: 0 auto;
        // padding: 0 20px;
        &__container{
            gap: 83px;
        }
        &__title{
        font-size: 30px;
        }
        &__cases{
            gap: 71px;
        }
    }
    .case{
        // max-width: 227px;
        &__title{
            font-size: 24px;
            margin-bottom: 23px;
        }
        &__text{
            margin-bottom: 31px;
            font-size: 14px;
        }
        &__fare{
            font-size: 22px;
            padding: 5px 15px;
            gap: 16px;
            &::after{
                font-size: 16px;
            }
        }
        &__photo{
            max-width: 210px;
        }
    }
}
@media (min-width: 767px){
    .services{
        // margin: 0 auto;
        &__container{
            gap: 31px;
            // padding: 0 30px;
        }
        &__cases{
            justify-content: space-between;
            flex-direction: row;
            padding: 25px;
            align-items: stretch;
        }
    }
    .case{
        flex-basis: calc((100% - 60px) / 3);
        align-items: flex-start;
        &__english{
            margin-bottom: 30px;
        }
        &__title{ 
            font-size: 20px;
            margin-bottom: 15px;
            text-align: left;
        }
        &__text{
            margin-bottom: 23px;
            // max-height: 84px;
            text-align: left;
            flex-grow: 1;
        }
        &__fare{
            font-size: 22px;
            padding: 5px 27px 7px 12px;
            gap: 10px;
            // flex-basis: calc(100% - (30px + 70px + 90px));
            align-items: center; 
            // flex-shrink: 0; 
        }
        // &__photo{
        //     margin-bottom: 23px; 
        // }
        &__english{
            padding: 5px 0 23px 0;
        }
        &__english-img{
            height:197px;
        }
        &__lesson{
            padding: 7px 0 50px 0;
        }
        &__lesson-img{
            height: 168px;
        }
        &__webinar{
            padding-bottom: 15px;
        }
        &__webinar-img{
            height: 210px;
        }
        &__photo{
            margin-bottom: 0;
        }
    }
}
@media (min-width: 992px){
    .services{
        // margin: 0 auto;
        &__title{
            font-size: 36px;
            }
        &__container{
            gap: 22px;
        }
        &__cases{
            padding: 32px;
        }
    }
    .case{
        max-width: 261px;
        // align-items: normal;
        // &__english{
        //     margin-bottom: 25px;
        // }
        &__title{ 
            font-size: 22px;
            margin-bottom: 15px;
        }
        &__text{
            margin-bottom: 31px;
            font-size: 16px;  
        }
        &__fare{
            font-size: 24px;
            padding: 5px 12px;
            // max-width: 205px;
        }
        &__english{
            padding: 12px 0 23px 0;
        }
        &__english-img{
            height:222px;
        }
        &__lesson{
            padding: 37px 0 22px 0;
        }
        &__lesson-img{
            height: 195px;
        }
        &__webinar{
            padding-bottom: 12px;
        }
        &__webinar-img{
            height: 242px;
        }
    }
}
@media (min-width: 1600px){
    .services{
        // margin: 0 auto;
        &__title{
            font-size: 48px;
            }
        &__container{
            gap: 50px;
        }
        &__cases{
